import React from "react";
import "./Title.css";
// as props voc pode dar o nome que quiser, porm tome cuidado, no v me locoloar no seu portiflio!
const Title = ({ titleText, color = "", additionalClass = "" }) => {
  return (
        <h1 
            className={ `title ${additionalClass}` } style={{color: color}}
        >
            {titleText}
            <hr 
                className="title__underscore"
                style={{borderColor: color}}
            />
        </h1>
    );

};

export default Title;
